-->
<template>
    <div class="page page-current">
        <header class="bar bar-nav">
            <h1 class="title"></h1>
        </header>
        <!-- <div class="topTips warning"><i class="icon-warning"></i></div> -->
        <bottomBar></bottomBar>
        <div class="content">
            <!--  -->
            <div class="description">
                <br></div>
            <div class="main-box">
                <div class="box-hd">
                    <span class="text"></span></div>
                <div class="box-bd">
                    <ul class="pro-list" v-if="taskTotal>0">
                        <li v-for="item in taskList" :key="item.id" class="item clearfix">
                            <div class="img-wrap">
                                <img src="/static/demo/demo.jpg" alt="">
                            </div>
                            <div class="inner">
                                <div class="name">{{item.name}}</div>
                                <div class="state">
                                    <span>3-4</span>
                                    <span></span>
                                </div>
                                <div class="info">
                                    <span class="attr"><span class="money"><i></i>398</span> </span>
                                    <span class="attr">3</span>
                                </div>
                                <div class="ctrl">
                                    <span class="stock">32</span>
                                    <a href="javascipt:;" class="button" @click.stop="getOrderFun(item)"></a>
                                </div>
                            </div>
                        </li>
                    </ul>
                    <div class="no-tmpl" v-else>
                        <span class="icon"><i class="icon-addtmpl"></i></span>
                        <span class="text"></span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import bottomBar from '@/components/bottomBar';
let $ = window.$;
export default {
    name: 'homeSeller',
    data() {
        return {
            taskList: [],
            taskTotal: 0
        };
    },
    methods: {
        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-11
         * @lastTime    2018-06-11
         * @description 
         */
        getTaskFun() {
            this.$http.get('/task/list', {
                params: {
                    pageIndex: 1,
                    pageSize: 10
                }
            }).then((response) => {
                if (response.data.success) {
                    this.taskList = response.data.data.list;
                    this.taskTotal = response.data.data.total;
                }
            });
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-07-01
         * @lastTime    2018-07-01
         * @description 
         */
        getOrderFun(item) {
            $.showPreloader();
            this.$http.post('/order/receive', {
                id: item.id
            }).then((response) => {
                $.hidePreloader();
                if (response.data.success) {
                    $.toast('');
                } else {
                    $.alert(response.data.message);
                }
            }).catch(() => {
                $.hidePreloader();
            });
        }
    },
    components: {
        bottomBar
    }
};

</script>



