<template>
    <div class="page page-current">
        <header class="bar bar-nav">
            <h1 class="title"></h1>
        </header>
        <bottomBar></bottomBar>
        <div class="content">
            <div class="main-box" v-if="total>0">
                <div class="box-hd">
                    <span class="text"></span>
                </div>
                <div class="box-bd">
                    <ul class="order-list">
                        <li class="item clearfix">
                            <div class="img-wrap">
                                <img src="/static/demo/demo.jpg" alt="">
                            </div>
                            <div class="inner">
                                <div class="name"></div>
                                <div class="state">
                                    <span>3-4</span>
                                    <span></span>
                                    <span class="right"></span>
                                </div>
                                <div class="money">
                                    <span></span>398
                                </div>
                            </div>
                            <div class="detail">
                                <span class="text"></span>
                                <span class="attr">
                                    <span class="name"></span>
                                <span class="val">3</span>
                                </span>
                                <span class="attr">
                                    <span class="name"></span>
                                <span class="val">3</span>
                                </span>
                            </div>
                            <div class="ctrl">
                                <a href="javascipt:;" class="button"></a>
                                <a href="javascipt:;" class="button"></a>
                                <a href="javascipt:;" class="button"></a>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="no-tmpl" v-else>
                <router-link to="/home">
                    <span class="icon"><i class="icon-addtmpl"></i></span>
                    <span class="text"></span>
                </router-link>
            </div>
            <!--  -->
            <!--  <div class="infinite-scroll-preloader">
                <div class="preloader"></div>
            </div> -->
        </div>
    </div>
</template>
<script>
import bottomBar from '@/components/bottomBar';
let $ = window.$;
export default {
    name: 'orderbuyer',
    data() {
        return {
            listData: [],
            total: 0
        };
    },
    created() {
        this.getOrderListFun();
    },
    methods: {
        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-11
         * @lastTime    2018-06-11
         * @description 
         */
        getOrderListFun() {
            $.showPreloader();
            this.$http.get('/task/list', {
                params: {
                    pageIndex: 1,
                    pageSize: 10
                }
            }).then((response) => {
                $.hidePreloader();
                if (response.data.success) {
                    this.listData = response.data.data.list;
                    this.total = response.data.data.total;
                }
            }).catch(() => {
                $.hidePreloader();
            });
        }
    },
    components: {
        bottomBar
    }
};

</script>



