* @Last Modified by:   weiberzeng
* @Last Modified time: 2018-06-11 23:15:22
-->
<template>
<div class="page page-current">
    <header class="bar bar-nav">
        <h1 class="title"></h1>
    </header>
    <div class="bar bar-header-secondary-home">
        <router-link to="/home/create">
            <span class="icon icon-home"></span>
            <span class="tab-label"></span>
        </router-link>
        <router-link to="/home/friend">
            <span class="icon icon-home"></span>
            <span class="tab-label"></span>
        </router-link>
    </div>
    <!-- <div class="topTips warning"><i class="icon-warning"></i></div> -->
    <bottomBar></bottomBar>
    <div class="content">
        <!--  -->
        <div class="description">
            <br> 1
            <br> 21 5
            <br> 3
        </div>
        <div class="main-box">
            <div class="box-hd">
                <span class="text"></span></div>
            <div class="box-bd">
                <ul class="tmpl-list clearfix" v-if="templateTotal>0">
                    <li class="item" :key="item.templateId" :class="{active:item.showMore}" v-for="item in templateData">
                        <div class="inner" @click.stop="checkedFun(item)">
                            <div class="img-wrap">
                                <img :src="item.goodsPicUrl" alt="">
                            </div>
                            <div class="name">{{item.storeName}}</div>
                            <div class="ctrl">
                                <span class="money"><i></i>{{item.goodsPrice}}</span>
                                <a href="javascipt:;" @click.stop="showMoreFun(item)"><i class="icon-more"></i></a>
                            </div>
                            <span class="checkbox" :class="{'is-checked':item.checked}"></span>
                        </div>
                        <div class="mask" @click.stop="item.showMore=false">
                            <a href="javascipt:;" class="edit-btn" @click.stop="modifyTempFun(item)"></a>
                            <a href="javascipt:;" class="del-btn" @click.stop="delTempFun(item)"></a>
                        </div>
                    </li>
                </ul>
                <div class="no-tmpl" v-else>
                    <router-link to="/home/create">
                        <span class="icon"><i class="icon-addtmpl"></i></span>
                        <span class="text"></span>
                    </router-link>
                </div>
            </div>
        </div>
        <div class="main-box">
            <div class="box-hd">
                <span class="text"></span></div>
            <div class="box-bd">
                <div class="list-block">
                    <ul>
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-title label"></div>
                                    <div class="item-input">
                                        <el-radio-group v-model="radio2">
                                            <el-radio :label="3"></el-radio>
                                            <el-radio :label="6"></el-radio>
                                            <el-radio :label="9"></el-radio>
                                        </el-radio-group>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="main-box">
            <div class="box-hd">
                <span class="text"></span></div>
            <div class="box-bd">
                <div class="list-block">
                    <ul>
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-title label"></div>
                                    <div class="item-input">
                                        <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange"></el-checkbox>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
                <div class="province-wrap">
                    <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                        <div class="clearfix">
                            <template v-for="(city,index) in cities" v-if="index<=21">
                                    <el-checkbox :label="city" :key="city">{{city}}</el-checkbox>
                                </template>
                        </div>
                        <div class="clearfix half">
                            <template v-for="(city,index) in cities" v-if="index>21&&index<=25">
                                    <el-checkbox :label="city" :key="city">{{city}}</el-checkbox>
                                </template>
                        </div>
                        <div class="clearfix all">
                            <template v-for="(city,index) in cities" v-if="index===26">
                                    <el-checkbox :label="city" :key="city">{{city}}</el-checkbox>
                                </template>
                        </div>
                        <div class="clearfix">
                            <template v-for="(city,index) in cities" v-if="index>26&&index<=30">
                                    <el-checkbox :label="city" :key="city">{{city}}</el-checkbox>
                                </template>
                        </div>
                        <div class="clearfix">
                            <template v-for="(city,index) in cities" v-if="index>30">
                                    <el-checkbox :label="city" :key="city">{{city}}</el-checkbox>
                                </template>
                        </div>
                    </el-checkbox-group>
                </div>
            </div>
        </div>
        <div class="main-box">
            <div class="box-hd">
                <span class="text"></span></div>
            <div class="box-bd">
                <div class="list-block">
                    <ul>
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-title label"></div>
                                    <div class="item-input">
                                        <input type="text" placeholder="">
                                    </div>
                                </div>
                            </div>
                        </li>
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-title label"></div>
                                    <div class="item-input">
                                        <input type="text" placeholder="">
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="main-box">
            <div class="box-hd">
                <span class="text"></span></div>
            <div class="box-bd">
                <div class="list-block">
                    <ul>
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-input">
                                        <textarea placeholder=""></textarea>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <br>
    </div>
</div>
</template>
<script>
import bottomBar from '@/components/bottomBar';
let $ = window.$;
// 
const cityOptions = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''];

export default {
    name: 'homeBuyer',
    data() {
        return {
            checkAll: false,
            checkedCities: [],
            cities: cityOptions,
            isIndeterminate: true,
            radio2: 3,
            templateData: [],
            templateTotal: 0
        };
    },
    created() {
        this.getTemplateFun();
    },
    methods: {
        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-11
         * @lastTime    2018-06-11
         * @description 
         */
        handleCheckAllChange(val) {
            this.checkedCities = val ? cityOptions : [];
            this.isIndeterminate = false;
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-11
         * @lastTime    2018-06-11
         * @description 
         */
        handleCheckedCitiesChange(value) {
            let checkedCount = value.length;
            this.checkAll = checkedCount === this.cities.length;
            this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-11
         * @lastTime    2018-06-11
         * @description 
         */
        getTemplateFun() {
            this.$http.get('/tasktemplate/list', {
                params: {
                    pageIndex: 1,
                    pageSize: 1000
                }
            }).then((response) => {
                if (response.data.success) {
                    let data = response.data.data.list;
                    for (let i in data) {
                        data[i].showMore = false;
                        data[i].checked = false;
                    }
                    this.templateData = data;
                    this.templateTotal = response.data.data.total;
                }
            });
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-11
         * @lastTime    2018-06-11
         * @description 
         */
        showMoreFun(item) {
            for (let i in this.templateData) {
                this.templateData[i].showMore = false;
            }
            item.showMore = true;
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-11
         * @lastTime    2018-06-11
         * @description 
         */
        checkedFun(item) {
            for (let i in this.templateData) {
                this.templateData[i].checked = false;
            }
            item.checked = true;
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-11
         * @lastTime    2018-06-11
         * @description 
         */
        delTempFun(item) {
            $.showPreloader();
            this.$http.get('/tasktemplate/delete', {
                params: {
                    templateId: item.templateId
                }
            }).then((response) => {
                $.hidePreloader();
                if (response.data.success) {
                    $.toast('');
                    this.getTemplateFun();
                } else {
                    $.alert(response.data.message);
                }
            }).catch(() => {
                $.hidePreloader();
            });
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-11
         * @lastTime    2018-06-11
         * @description 
         */
        modifyTempFun(item) {
            this.$router.replace({
                path: '/home/modify/' + item.templateId
            });
        }
    },
    components: {
        bottomBar
    }
};
</script>



